<template>
    <div class="Toconfiguretwo students">
        <div class="Toconfiguretwo-dv">
            <span class="spano">试卷配置</span> > <span class="spant">配置考查内容与课程目标关系</span>
        </div>
        <div class="Toconfiguretwo-dv1">
            <h2>《{{coursename}}》课程考查内容与课程目标对应关系</h2>
        </div>
        <div class="Toconfiguretwo-dv2" v-for="(item, indexq) in AllList" :key="indexq"> 
            <h2 class="Toconfiguretwo-dv2h2">{{item.name}}</h2>
            <table cellspacing="0"  cellpadding="0"  border="0" class="el-table el-table--border">
                <colgroup>
                    <col/>
                    <col/>
                    <col >
                </colgroup>
                <tr>
                    <th style=" width:50px; text-align: center;">序号</th>
                    <th style=" width:100px;text-align: center;">课程目标</th>
                    <th style=" width:600px;text-align: center;">考查内容（可以是试卷题号/考查知识点）</th>
                </tr>
                <tr v-for="(iteme, indexs) in item.examcodes" :key="indexs">
                    <td>
                        <span class="table-sop" >{{indexs + 1}}</span>
                    </td>
                    <td style="width: 189px;text-align: center;" >
                       <!-- 课程目标 -->
                       {{iteme.goalName}}
                    </td>
                    <td> 
                        <input onkeyup="this.value=this.value.replace(/[^\a-\z\A-\Z0-9\u4E00-\u9FA5\、\.-]/g,'')" v-model="iteme.examcodes" ref='id' style="height: 30px;margin-left: 8px; width: 98%;"  placeholder="请填写考查内容编号,只能输入（1、2、）的形式">
                    </td>
                </tr>
            </table>
        </div>
        <div class="Toconfiguretwo-dv3" v-if="type !== '查看'">
            <template>
                <el-button type="primary" @click="submit()">保 存</el-button>
            </template>
        </div>
    </div>
</template>
<script>
export default {
    name: "TestWorld",
    data(){
        return{
            content:'',
            AllList:[],
            videolist:[],
            course_id:this.$route.query.id,
            coursename:this.$route.query.name,
            type:this.$route.query.type,
            grade:this.$route.query.grade
        }
    },
    methods:{
        //  数据回显
        getexamgoal(){
            this.$post(`/teacher/examgoal/all2/${this.course_id}/${this.$route.query.teacherId}/${this.grade}`).then(res=>{
                if(res.status == 1){
                    this.AllList = res.data
                }
            })
        },
        //提交按钮
        submit(){
            let list= [];
            this.AllList.forEach((item,index) => {
                item.examcodes.forEach(items => {
                    list.push({
                        examineId:item.id,
                        courseId:this.course_id,
                        goalId: items.goalId,
                        examcodes: items.examcodes,
                    })
                })
            });
            this.videolist = list;
            this.$confirm('此操作将修改该课程试卷配置，需要重新上传该课程成绩', '提示', {
                confirmButtonText: '确定',
                cancelButtonText: '取消',
            }).then(() => {
                this.SaveSubmit(this.videolist)  // 调用保存的接口函数，确定保存
            }).catch(() => {  
                this.$message({
                    type: 'info',
                    message: '已取消修改'
                });
            });
        },
        SaveSubmit(key){   //保存接口
            this.$post('/teacher/examgoal/edit2',key).then(res=>{
                if(res.success === true){
                    this.$notify.success({
                        title: 'success',
                        duration:1500,
                        message: '保存成功'
                    })
                    this.$router.push({path: '/Construction/TestPaperConfiguration'});   
                }else{
                    this.$notify.error({
                        title: 'error',
                        duration:1500,
                        message: '保存失败'
                    })
                }
            })
        }
    },
    mounted(){
        this.getexamgoal()
    }
}
</script>
<style lang="scss" >
    .Toconfiguretwo{
        
        .Toconfiguretwo-dv{
            border-radius:0px 0px 10px 10px;
            padding: 33px 0;
            padding-left: 20px;
            font-size:16px;
            font-family:PingFang SC;
            font-weight:500;
            .spano{
                color: #696969;
            }
            .spant{
                color: #03B187;
            }
        }
        .Toconfiguretwo-dv1{
            height:86px;
            background:rgba(3,177,135,1);
            border-radius:10px 10px 0px 0px;
            line-height: 86px;
            text-align: center;
            h2{
                font-size:24px;
                font-family:PingFang SC;
                font-weight:bold;
                color:rgba(255,255,255,1);
            }
        }
        .Toconfiguretwo-dv2{
            padding: 0 40px;
            background-color: #fff;
            padding-bottom: 35px;
            .Toconfiguretwo-dv2h2{
                font-size:20px;
                font-family:PingFang SC;
                font-weight:bold;
                color:rgba(50,50,50,1);
                padding: 30px 0;
            }
            .el-table--border{
                td{
                    border-bottom: 1px solid #EBEEF5;
                    text-align: center;
                }
                
                th{
                    background:rgba(248,248,248,1);
                }
                input{
                    width: 100%;
                    height: 35px;
                    padding: 3px;
                    font-size:14px;
                    font-family:PingFang SC;
                    font-weight:500;
                    color:rgba(50,50,50,1);
                    /* 这里是去除掉input的默认样式然后修改为自己的 */
                    background:none;  
                    outline:none;  
                    // border:0px;
                    /* 这里是修改为自己的样式 */
                    border:1px solid rgba(238,238,238,1);
                    /* 使用怪异盒模型，也可以不使用 */
                    box-sizing: border-box;
                }
                input::-webkit-input-placeholder {
                    /* placeholder颜色  */
                    color: #dbe5f0;
                    /* placeholder字体大小  */
                    font-size: 12px;
                    /* placeholder位置  */
                    text-align: left;
                }
                /* 这里是设置input获得焦点时的变化 */
                input:focus{
                    // border-bottom: 2px solid #0F996B;
                    border-bottom-left-radius: 1px;
                    border-bottom-right-radius: 1px;
                }
            }
        }
        .Toconfiguretwo-dv3{
            margin: 40px auto;
            margin-bottom: 100px;
            text-align: center;
            .el-button--primary{
                width: 100px;
            }
        }
    }
</style>


